@font-face {
  font-family: HappyMonkey;
  src: url(./fonts/happymonkey/HappyMonkey-Regular.ttf);
}
  
.global-wrapper {
  font-family: HappyMonkey, Arial, Helvetica, sans-serif;
  color:aliceblue;
  height: 100vh;
  width: 100vw;

  /* background: rgb(18, 16, 17); */
  /* background: var(--gradient-main); */

  display: grid;
  grid-template-rows: 100px 1fr 60px;
  grid-template-columns: 10vw 1fr 10vw;
  grid-template-areas:
    " . header . "
    " . body   . "
    " . footer . ";
}

body {
	/* background: linear-gradient(-45deg, #080302, #b41010, #160a81, #10493c); */
  background: linear-gradient(-45deg, #f44320, #d80c0c, #bcbcbf, #f8ab04);
	background-size: 400% 400%;
	animation: gradient 15s ease infinite;
	height: 100vh;
}

@keyframes gradient {
	0% {
		background-position: 0% 50%;
	}
	50% {
		background-position: 100% 50%;
	}
	100% {
		background-position: 0% 50%;
	}
}

a {
  color: aliceblue;
}

header {
  margin-top: 30px;
  text-align: center;
  grid-area: header;
  border-bottom: 2px solid white;
}

header > h1 {
  font-weight: bold;
}

footer {
  grid-area: footer;
  border-top: 2px solid white;
  display: grid;
  grid-auto-flow: column;
  place-items: center;
}

.main-content {
  grid-area: body;
  overflow-y: scroll;
  /* hide scrollbar on IE, edge */
  -ms-overflow-style: none;
  /* hide scrollbar on firefox */
  scrollbar-width: none;
}

.rows {
  display: grid;
  grid-auto-flow: row;
}

.panel {
  text-align: center;
  padding-top: 20px;  
  /* border: 2px solid white; */
  position: absolute;
  width: 80vw;
  font-size: larger;
}

.slide {
  -webkit-animation: slide 0.8s forwards;
  /* -webkit-animation-delay: 2s; */
  animation: slide 0.8s forwards;
  /* animation-delay: 2s; */
}

.left {
  left: -60vw;
}

.right {
  left: 60vw;
}



@-webkit-keyframes slide {
    100% { left: 10vw; }
}

@keyframes slide {
    100% { left: 10vw; }
}
